.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-color: #121212;
  color: white;
}

/* Add animated music notes background */
.login::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(circle at 20% 30%, rgba(29, 185, 84, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(29, 185, 84, 0.05) 0%, transparent 50%);
  z-index: 0;
}

.login-content {
  max-width: 500px;
  text-align: center;
  padding: 2rem;
  background-color: rgba(18, 18, 18, 0.8);
  border-radius: 10px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.logo {
  width: 180px;
  margin-bottom: 2rem;
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.3));
}

.login h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #1DB954;
}

.login p {
  font-size: 1.1rem;
  margin-bottom: 2rem;
  color: #b3b3b3;
  line-height: 1.5;
}

.login-button {
  display: inline-block;
  background-color: #1DB954;
  color: white;
  padding: 0.8rem 2rem;
  border-radius: 30px;
  font-size: 1.1rem;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s;
  border: none;
  cursor: pointer;
}

.login-button:hover {
  background-color: #1ed760;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(29, 185, 84, 0.3);
}

.login-button:active {
  transform: translateY(0);
}

.login-button::after {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    to bottom right,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.1) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  transform: rotate(45deg);
  animation: shimmer 3s infinite;
}

@keyframes shimmer {
  0% { transform: translateX(-100%) rotate(45deg); }
  100% { transform: translateX(100%) rotate(45deg); }
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Add a footer with Spotify branding */
.login-footer {
  position: absolute;
  bottom: 1rem;
  text-align: center;
  font-size: 0.8rem;
  color: #535353;
}

.login-footer a {
  color: #1DB954;
  text-decoration: none;
}

.login-footer a:hover {
  text-decoration: underline;
}

/* Add responsive styles for smaller screens */
@media (max-width: 600px) {
  .login-content {
    width: 90%;
    padding: 1.5rem;
  }
  
  .login h1 {
    font-size: 2rem;
  }
  
  .login p {
    font-size: 1rem;
  }
} 